import React, { Component } from "react";

class SaldoAwalHutang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      tanggal: "",
      supplier: "",
      total_bruto: "",
      total_hutang_gr: "",
      total_hutang_rp: "",
      total_hutang_usd: "",
      total_titipan_gr: "",
      total_titipan_rp: "",
    };
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submit = () => {
    console.log(JSON.stringify(this.state));
  };
  render() {
    return (
      <div className="row">
        {/* Awal Field */}
        <div className="col-md-3">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Tanggal
            </label>
            <input
              id="saldo_awal_hutang_tanggal"
              name="saldo_awal_hutang_tanggal"
              type="date"
              className="form-control"
              placeholder="Tanggal"
              onChange={this.inputChange("tanggal")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-3">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Supplier
            </label>
            <select
              id="saldo_awal_hutang_supplier"
              name="saldo_awal_hutang_supplier"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              className="form-control"
              placeholder="Supplier"
              onChange={this.inputChange("supplier")}
            >
              <option value="">AMG</option>
              <option value="">AMG</option>
            </select>
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field Kosong */}
        <div className="col-lg-6"></div>
        {/* Akhir Field Kosong */}
        {/* Awal Field */}
        <div className="col-md-4">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Total Bruto
            </label>
            <input
              id="saldo_awal_hutang_total Bruto"
              name="saldo_awal_hutang_total Bruto"
              type="number"
              min={0}
              step={0.01}
              className="form-control"
              placeholder="Total Bruto"
              onChange={this.inputChange("total_bruto")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-4">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Total Hutang (Gr)
            </label>
            <input
              id="saldo_awal_hutang_total_hutang_gr"
              name="saldo_awal_hutang_total_hutang_gr"
              type="number"
              min={0}
              step={0.01}
              className="form-control"
              placeholder="Total Hutang(Gr)"
              onChange={this.inputChange("total_hutang_gr")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-4">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Total Hutang (Rp)
            </label>
            <input
              id="saldo_awal_hutang_total_hutang_rp"
              name="saldo_awal_hutang_total_hutang_rp"
              type="number"
              min={0}
              step={0.01}
              className="form-control"
              placeholder="Total Hutang(Rp)"
              onChange={this.inputChange("total_hutang_rp")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-4">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Total Hutang (USD)
            </label>
            <input
              id="saldo_awal_hutang_total_hutang_usd"
              name="saldo_awal_hutang_total_hutang_usd"
              type="number"
              min={0}
              step={0.01}
              className="form-control"
              placeholder="Total Hutang(USD)"
              onChange={this.inputChange("total_hutang_usd")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-4">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Total Titipan(Rp)
            </label>
            <input
              id="saldo_awal_hutang_total_titipan_rp"
              name="saldo_awal_hutang_total_titipan_rp"
              type="number"
              min={0}
              step={0.01}
              className="form-control"
              placeholder="0"
              onChange={this.inputChange("total_titipan_rp")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-4">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Total Titipan(Gr)
            </label>
            <input
              id="saldo_awal_hutang_total_titipan_gr"
              name="saldo_awal_hutang_total_titipan_gr"
              type="number"
              min={0}
              step={0.01}
              className="form-control"
              placeholder="0"
              onChange={this.inputChange("total_titipan_gr")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Button */}
        <div className="col-md-12 col-lg-12">
          <div className="text-right">
            <button
              type="button"
              className="btn btn-info"
              onClick={this.submit}
            >
              Submit
            </button>
          </div>
        </div>
        {/* Akhir Button */}
      </div>
    );
  }
}

export default SaldoAwalHutang;
