import React, { Component } from "react";

class SaldoAwalStok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      tanggal: "",
      nama_lokasi: "",
      kode_barang: "",
      kadar: "",
      berat: "",
    };
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submit = () => {
    console.log(JSON.stringify(this.state));
  };
  render() {
    return (
      <div className="row">
        {/* Awal Field */}
        <div className="col-md-3">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Tanggal
            </label>
            <input
              id="saldo_awal_stok_tanggal"
              name="saldo_awal_stok_tanggal"
              type="date"
              className="form-control"
              placeholder="Tanggal"
              onChange={this.inputChange("tanggal")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-3">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Nama Lokasi
            </label>
            <select
              id="saldo_awal_stok_nama_lokasi"
              name="saldo_awal_stok_nama_lokasi"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              className="form-control"
              placeholder="Nama Lokasi"
              onChange={this.inputChange("nama_lokasi")}
            >
              <option value="">AMG</option>
              <option value="">AMG</option>
            </select>
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field Kosong */}
        <div className="col-lg-6"></div>
        {/* Akhir Field Kosong */}
        {/* Awal Field */}
        <div className="col-md-4">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Kode Barang
            </label>
            <select
              id="saldo_awal_stok_kode_barang"
              name="saldo_awal_stok_kode_barang"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              className="form-control"
              placeholder="Kode Barang"
              onChange={this.inputChange("kode_barang")}
            >
              <option value="">100PER02</option>
              <option value="">100PER02</option>
            </select>
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-4">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Kadar (%)
            </label>
            <input
              id="saldo_awal_stok_kadar"
              name="saldo_awal_stok_kadar"
              type="number"
              step={0.01}
              min={0}
              max={100}
              className="form-control"
              placeholder="75.00"
              onChange={this.inputChange("kadar")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-4">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Berat (Gr)
            </label>
            <input
              id="saldo_awal_stok_berat"
              name="saldo_awal_stok_berat"
              type="number"
              step={0.01}
              min={0}
              className="form-control"
              placeholder="0"
              onChange={this.inputChange("berat")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Button */}
        <div className="col-md-12 col-lg-12">
          <div className="text-right">
            <button
              type="button"
              className="btn btn-info"
              onClick={this.submit}
            >
              Submit
            </button>
          </div>
        </div>
        {/* Akhir Button */}
      </div>
    );
  }
}

export default SaldoAwalStok;
