import React, { Component } from "react";

class SaldoAwalPiutang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      tanggal: "",
      sales: "",
      nomor_toko: "",
      detail: "",
      tanggal_faktur: "",
      nomor_bon: "",
      bruto: "",
      netto: "",
      saldo_gr: "",
      saldo_rp: "",
      titip_gr: "",
      titip_rp: "",
    };
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submit = () => {
    console.log(JSON.stringify(this.state));
  };
  render() {
    return (
      <div className="row">
        {/* Awal Field */}
        <div className="col-md-3">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Tanggal
            </label>
            <input
              id="saldo_awal_piutang_tanggal"
              name="saldo_awal_piutang_tanggal"
              type="date"
              className="form-control"
              placeholder="Tanggal"
              onChange={this.inputChange("tanggal")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-3">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Sales
            </label>
            <select
              id="saldo_awal_piutang_sales"
              name="saldo_awal_piutang_sales"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              className="form-control"
              onChange={this.inputChange("sales")}
            >
              <option value="">Bambang</option>
              <option value="">Bambang</option>
            </select>
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field Kosong */}
        <div className="col-lg-6"></div>
        {/* Akhir Field Kosong */}
        {/* Awal Field */}
        <div className="col-md-3">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Nama Toko
            </label>
            <input
              id="saldo_awal_piutang_nama_toko"
              name="saldo_awal_piutang_nama_toko"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              className="form-control"
              placeholder="Toko Makmur"
              onChange={this.inputChange("nama_toko")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-3">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Detail
            </label>
            <input
              id="saldo_awal_piutang_detail"
              name="saldo_awal_piutang_detail"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              className="form-control"
              placeholder="Toko Makmur"
              onChange={this.inputChange("detail")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-3">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Tanggal faktur
            </label>
            <input
              id="saldo_awal_piutang_tanggal_faktur"
              name="saldo_awal_piutang_tanggal_faktur"
              type="date"
              className="form-control"
              onChange={this.inputChange("tanggal_faktur")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-3">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Nomor Bon
            </label>
            <input
              id="saldo_awal_piutang_no_bon"
              name="saldo_awal_piutang_no_bon"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              className="form-control"
              placeholder="BON001"
              onChange={this.inputChange("nomor_bon")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-2">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Bruto (Gr)
            </label>
            <input
              id="saldo_awal_piutang_bruto_gr"
              name="saldo_awal_piutang_bruto_gr"
              type="number"
              min={0}
              step={0.01}
              className="form-control"
              placeholder="0.01"
              onChange={this.inputChange("bruto")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-2">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Netto (Gr)
            </label>
            <input
              id="saldo_awal_piutang_netto_gr"
              name="saldo_awal_piutang_netto_gr"
              type="number"
              min={0}
              step={0.01}
              className="form-control"
              placeholder="0.01"
              onChange={this.inputChange("netto")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-2">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Saldo (Gr)
            </label>
            <input
              id="saldo_awal_piutang_saldo_gr"
              name="saldo_awal_piutang_saldo_gr"
              type="number"
              min={0}
              step={0.01}
              className="form-control"
              placeholder="0.01"
              onChange={this.inputChange("saldo_gr")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-2">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Saldo (Rp)
            </label>
            <input
              id="saldo_awal_piutang_saldo_rp"
              name="saldo_awal_piutang_saldo_rp"
              type="number"
              min={0}
              step={0.01}
              className="form-control"
              placeholder="0.01"
              onChange={this.inputChange("saldo_rp")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-2">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Titip (Rp)
            </label>
            <input
              id="saldo_awal_piutang_titip_rp"
              name="saldo_awal_piutang_titip_rp"
              type="number"
              min={0}
              step={0.01}
              className="form-control"
              placeholder="0.01"
              onChange={this.inputChange("titip_rp")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-2">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Titip (Gr)
            </label>
            <input
              id="saldo_awal_piutang_titip_gr"
              name="saldo_awal_piutang_titip_gr"
              type="number"
              min={0}
              step={0.01}
              className="form-control"
              placeholder="0.01"
              onChange={this.inputChange("titip_gr")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Button */}
        <div className="col-lg-12 float-right">
          <div className="text-right">
            <button
              type="button"
              className="btn btn-info"
              onClick={this.submit}
            >
              Submit
            </button>
          </div>
        </div>
        {/* Akhir */}
      </div>
    );
  }
}

export default SaldoAwalPiutang;
