import React, { Component } from "react";

class SaldoAwalCT extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      tanggal: "",
      kode: "",
      bruto: "",
      netto: "",
    };
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submit = () => {
    console.log(JSON.stringify(this.state));
  };
  render() {
    return (
      <div className="row">
        {/* Awal Field */}
        <div className="col-md-3">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Tanggal
            </label>
            <input
              id="saldo_awal_ct_tanggal"
              name="saldo_awal_ct_tanggal"
              type="date"
              className="form-control"
              placeholder="Tanggal"
              onChange={this.inputChange("tanggal")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-3">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Kode
            </label>
            <select
              id="saldo_awal_ct_kode"
              name="saldo_awal_ct_kode"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              className="form-control"
              placeholder="Supplier"
              onChange={this.inputChange("kode")}
            >
              <option value="">CK02</option>
              <option value="">CK01</option>
            </select>
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-3">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Bruto
            </label>
            <input
              id="saldo_awal_ct_bruto"
              name="saldo_awal_ct_bruto"
              type="number"
              min={0}
              step={0.01}
              className="form-control"
              placeholder="0.00"
              onChange={this.inputChange("bruto")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Field */}
        <div className="col-md-3">
          <div className="form-group">
            <label htmlFor="" className="text-black">
              Netto
            </label>
            <input
              id="saldo_awal_ct_netto"
              name="saldo_awal_ct_netto"
              type="number"
              min={0}
              step={0.001}
              className="form-control"
              placeholder="0.000"
              onChange={this.inputChange("netto")}
            />
          </div>
        </div>
        {/* Akhir Field */}
        {/* Awal Button */}
        <div className="col-lg-12 float-right">
          <div className="text-right">
            <button
              type="button"
              className="btn btn-info"
              onClick={this.submit}
            >
              Submit
            </button>
          </div>
        </div>
        {/* Akhir */}
      </div>
    );
  }
}
export default SaldoAwalCT;
